<template>
  <div class="w-50 mx-auto">

      <button class="btn-info w-50" @click="onClickStart">
          {{ current === 0 ? 'Start' : 'Następne' }}
      </button>
      <div class="question mt-3"  v-if="showOptions">
          <p >{{ activeTask.question.question }}</p>
          <div class="" v-for="option in activeTask.options" :key="option">
              <button class="btn-success w-75 text-start" @click="onClickStop">{{ option }}</button>
          </div>

      </div>
      <div class="question mt-3" v-if="totalTime">
          <p>{{totalTime}} s</p>
      </div>
      <div v-if="showResult">
          <p>{{this.times}} s</p>
      </div>
  </div>
</template>
<script>
export default {
    name: "main-table",
    data() {
        return {
            showOptions: false,
            activeTask: null,
            current: 0,
            questions: [
                    {question:'Znajdź swój koszyk z zakupami', answer:'Moje zakupy'},
                    {question:'Znajdź dane kontaktowe firmy', answer:'Kontakt'},
                    {question:'Znajdź informacje o firmie', answer:'O firmie'},
                    {question:'Znajdź swój profil', answer:'Mój profil'},
                    {question:'Znajdź historię wiadomości', answer:'Wiadomości'},
                    {question:'Znajdź aktualne promocje', answer:'Promocje'}
                ],
            tasks:[], // Question, options
            times:[],
            numberOfOptions: 3,
            numbersOfOptions:[3, 3, 3, 5, 5, 5, 7, 7, 7, 10, 10, 10, 15, 15, 15],
            options: {1:[]}, // question: [options],
            correct: {1:"Moje zakupy", 2:"Kontakt", 3:"O firmie", 4:"Mój profil", 5:"Wiadomości"}, // question: correctOption
            timeStart: 0,
            timeFinish: 0,
            totalTime: null,
            allOptions:[
                "Strona główna", "Mój profil", "Kategorie", "Kontakt", "Moje zakupy", "O firmie", "Asortyment",
                "Wiadomości", "Muzyka", "Obrazy", "Literatura", "Promocje", "Dostawa", "Płatności", "Podstawa prawna"],
            showResult: false

        };
    },
    methods: {
        generateTask(optionsNo) {
            const question = this.questions[Math.floor(Math.random() * this.questions.length)]
            let options = [question.answer]
            const addOptions = this.allOptions.filter(o => o !== question.answer).slice(1, optionsNo)
            console.log('add ', addOptions)
            options = options.concat(addOptions)

            let sorted = options.sort()

            return {question:question, options:sorted}
        },
        generateTasks(){
            for (let i=0; i < this.numbersOfOptions.length; i++) {
                this.tasks.push(this.generateTask(this.numbersOfOptions[i]))
            }
        },
        updateTest(){
            console.log('Task ', this.activeTask)
            this.activeTask = this.tasks[this.current]
            this.current+=1
        },
        getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min)) + min;
        },
        onClickStart(){
            if (this.current < this.numbersOfOptions.length) {
                this.updateTest()
                this.showOptions = true
                this.timeStart = Date.now()
            }
            else {
                this.showResult = true
            }
        },
        onClickStop(){
            this.timeFinish = Date.now()
            this.totalTime = (this.timeFinish-this.timeStart)/1000;
            this.times.push(this.totalTime)
            this.showOptions=false
        }
    },
    mounted() {
        this.generateTasks()
    },
};
</script>
<style scoped>
.question {
    font-size: large;
}
</style>
